(function() {
  'use strict';

  angular
    .module('madBackWeb')
    .controller('subNavbarCtrl', subNavbarCtrl);

  /** @ngInject */
  function subNavbarCtrl($scope, $location) {
    $scope.user = false;
    $scope.advert = false;

    $scope.userlist = false;
    $scope.adduser = false;
    $scope.userauth = false;

    $scope.adpush = false;
    $scope.adaduit = false;
    $scope.adsearch = false;

    $scope.finance = false;
    $scope.financecash = false;
    $scope.financerefund = false;

    if ($location.path() == '/user/list-mobile') {
      $scope.user = true;
    }
    if ($location.path() == '/user/list-mobileuser/list-advert') {
      $scope.advert = true;
    }

    if ($location.path() == '/admin/auth') {
      $scope.userauth = true;
    }
    if ($location.path() == '/admin/userlist') {
      $scope.userlist = true;
    }
    if ($location.path() == '/admin/adduser') {
      $scope.adduser = true;
    }

    if ($location.path() == '/advert/push') {
      $scope.adpush = true;
    }
    if ($location.path() == '/advert/audit') {
      $scope.adaduit = true;
    }
    if ($location.path() == '/advert/search') {
      $scope.adsearch = true;
    }

    if ($location.path() == '/finance') {
      $scope.finance = true;
    }

    if ($location.path() == '/finance/cash') {
      $scope.financecash = true;
    }

    if ($location.path() == '/finance/refund') {
      $scope.financerefund = true;
    }

  }

})();

(function() {
  'use strict';

  angular
    .module('madBackWeb')
    .directive('acmeNavbar', acmeNavbar);

  /** @ngInject */
  function acmeNavbar() {
    var directive = {
      restrict: 'E',
      templateUrl: 'app/components/navbar/navbar.html',
      scope: {
          creationDate: '='
      },
      controller: NavbarController,
      controllerAs: 'vm',
      bindToController: true
    };

    return directive;

    /** @ngInject */
    function NavbarController(moment) {
      var vm = this;

      // "vm.creation" is avaible by directive option "bindToController: true"
      vm.relativeDate = moment(vm.creationDate).fromNow();
    }
  }

})();

(function() {
  'use strict';

  angular
    .module('madBackWeb')
    .controller('navbarCtrl', navbarCtrl);

  /** @ngInject */
  function navbarCtrl($scope, $location, $state) {
    $scope.isMain = false;
    $scope.isUserManage = false;
    $scope.isAdManage = false;
    $scope.isFinanceManage = false;
    $scope.isAdmin = false;
    $scope.isStatistics = false;
    
    console.log($location.path());
    if ($location.path() == '/') {
      $scope.isMain = true;
    }

    if ($location.path() == '/user/list-mobile' ||
        $location.path() == '/user/list-mobileuser/list-advert' ||
        $location.path() == '/user/list-mobileuser/create') {
      $scope.isUserManage = true;
    }

    if ($location.path() == '/advert/push' ||
        $location.path() == '/advert/audit' ||
        $location.path() == '/advert/search') {
      $scope.isAdManage = true;
    }

    if ($location.path() == '/finance' ||
        $location.path() == '/finance/cash' ||
        $location.path() == '/finance/refund') {
      $scope.isFinanceManage = true;
    }

    if ($location.path() == '/admin/userlist' ||
        $location.path() == '/admin/adduser' ||
        $location.path() == '/admin/auth') {
      $scope.isAdmin = true;
    }

    if ($location.path() == '/statistics') {
      $scope.isStatistics = true;
    }
  }

})();

(function() {
  'use strict';

  angular
    .module('madBackWeb')
    .directive('acmeMalarkey', acmeMalarkey);

  /** @ngInject */
  function acmeMalarkey(malarkey) {
    var directive = {
      restrict: 'E',
      scope: {
        extraValues: '='
      },
      template: '&nbsp;',
      link: linkFunc,
      controller: MalarkeyController,
      controllerAs: 'vm'
    };

    return directive;

    function linkFunc(scope, el, attr, vm) {
      var watcher;
      var typist = malarkey(el[0], {
        typeSpeed: 40,
        deleteSpeed: 40,
        pauseDelay: 800,
        loop: true,
        postfix: ' '
      });

      el.addClass('acme-malarkey');

      angular.forEach(scope.extraValues, function(value) {
        typist.type(value).pause().delete();
      });

      watcher = scope.$watch('vm.contributors', function() {
        angular.forEach(vm.contributors, function(contributor) {
          typist.type(contributor.login).pause().delete();
        });
      });

      scope.$on('$destroy', function () {
        watcher();
      });
    }

    /** @ngInject */
    function MalarkeyController($log, githubContributor) {
      var vm = this;

      vm.contributors = [];

      activate();

      function activate() {
        return getContributors().then(function() {
          $log.info('Activated Contributors View');
        });
      }

      function getContributors() {
        return githubContributor.getContributors(10).then(function(data) {
          vm.contributors = data;

          return vm.contributors;
        });
      }
    }

  }

})();

